import { useEffect, useRef } from "react";
import * as THREE from "three";

// re-use for instance computations
const scratchObject3D = new THREE.Object3D();

export const useData = (length) => {
  const meshRef = useRef();

  useEffect(() => {
    if (!meshRef.current) return;
    const halfLength = Math.floor(length / 2);
    // set the transform matrix for each instance
    let count = 0;
    for (let i = -length; i < length; i++) {
      for (let j = -length; j < length; j++) {
        for (let k = -halfLength; k < halfLength; k++) {
          if (i === 0 && j === 0 && k === halfLength - 1) continue;
          scratchObject3D.position.set(i, j, k);
          scratchObject3D.updateMatrix();
          meshRef.current.setMatrixAt(count, scratchObject3D.matrix);
          count++;
        }
      }
    }
    meshRef.current.instanceMatrix.needsUpdate = true;
  }, [length]);

  return meshRef;
};
