import { Box } from "@react-three/drei";
import BoxBorder from "./BoxBorder";

export default function OuterBox() {
  return (
    <>
      <Box args={[1, 1, 1]} position={[0, 0, 0]}>
        <meshBasicMaterial attach="material" color="black" />
      </Box>
      <BoxBorder />
    </>
  );
}
