import { useEffect, useMemo, useRef } from "react";

export default function TesseractGeometry({ r }) {
  const ref = useRef();

  const positions = useMemo(() => {
    const r1 = r / 3.5;
    const r2 = r1 / 25;
    const r3 = r;
    const r4 = 0;
    const x = 0;
    const y = 0;
    const z = 0;

    const startX = x - r1 / 2;
    const startY = y - r1 / 2;
    const startZ = z - r1 / 2;

    const startX2 = x - r3 / 2;
    const startY2 = y - r3 / 2;
    const startZ2 = z - r3 / 2;

    const vertices = [
      //back outer vertices 0-3
      [startX, startY, startZ],
      [startX + r1, startY, startZ],
      [startX, startY + r1, startZ],
      [startX + r1, startY + r1, startZ],

      //front outer vertices 4-7
      [startX, startY, startZ + r1],
      [startX + r1, startY, startZ + r1],
      [startX, startY + r1, startZ + r1],
      [startX + r1, startY + r1, startZ + r1],

      //front inner vertices 8-11
      [startX + r2, startY + r2, startZ + r1],
      [startX + r1 - r2, startY + r2, startZ + r1],
      [startX + r2, startY + r1 - r2, startZ + r1],
      [startX + r1 - r2, startY + r1 - r2, startZ + r1],

      //right inner vertices 12-15
      [startX + r1, startY + r2, startZ + r1 - r2],
      [startX + r1, startY + r2, startZ + r2],
      [startX + r1, startY + r1 - r2, startZ + r1 - r2],
      [startX + r1, startY + r1 - r2, startZ + r2],

      //left inner vertices 16-19
      [startX, startY + r2, startZ + r2],
      [startX, startY + r2, startZ + r1 - r2],
      [startX, startY + r1 - r2, startZ + r2],
      [startX, startY + r1 - r2, startZ + r1 - r2],

      //top inner vertices 20-23
      [startX + r2, startY + r1, startZ + r1 - r2],
      [startX + r1 - r2, startY + r1, startZ + r1 - r2],
      [startX + r2, startY + r1, startZ + r2],
      [startX + r1 - r2, startY + r1, startZ + r2],

      //botom inner vertices 24-27
      [startX + r2, startY, startZ + r2],
      [startX + r1 - r2, startY, startZ + r2],
      [startX + r2, startY, startZ + r1 - r2],
      [startX + r1 - r2, startY, startZ + r1 - r2],
      //back inner vertices 28-31
      [startX + r2, startY + r2, startZ],
      [startX + r1 - r2, startY + r2, startZ],
      [startX + r2, startY + r1 - r2, startZ],
      [startX + r1 - r2, startY + r1 - r2, startZ],
      //front top right pole 32-34
      [startX2 + r3 - r4, startY2 + r3 - r4, startZ2 + r3], //11 extended
      [startX2 + r3 - r4, startY2 + r3, startZ2 + r3 - r4], //21 extended
      [startX2 + r3, startY2 + r3 - r4, startZ2 + r3 - r4], //14 extended
      //front bottom right pole 35-37
      [startX2 + r3 - r4, startY2 + r4, startZ2 + r3], //9
      [startX2 + r3, startY2 + r4, startZ2 + r3 - r4], //12
      [startX2 + r3 - r4, startY2, startZ2 + r3 - r4], //27
      //front bottom left pole 38-40
      [startX2 + r4, startY2 + r4, startZ2 + r3], //8
      [startX2, startY2 + r4, startZ2 + r3 - r4], //17
      [startX2 + r4, startY2, startZ2 + r3 - r4], //26
      //front top left pole 41-43
      [startX2, startY2 + r3 - r4, startZ2 + r3 - r4], //19
      [startX2 + r4, startY2 + r3, startZ2 + r3 - r4], //20
      [startX2 + r4, startY2 + r3 - r4, startZ2 + r3], //10
      //back top right pole 44-46
      [startX2 + r3 - r4, startY2 + r3 - r4, startZ2], //31
      [startX2 + r3 - r4, startY2 + r3, startZ2 + r4], //23
      [startX2 + r3, startY2 + r3 - r4, startZ2 + r4], //15
      //back bottom right pole 47-49
      [startX2 + r3 - r4, startY2, startZ2 + r4], //25
      [startX2 + r3, startY2 + r4, startZ2 + r4], //13
      [startX2 + r3 - r4, startY2 + r4, startZ2], //29
      //back bottom left pole 50-52
      [startX2, startY2 + r4, startZ2 + r4], //16
      [startX2 + r4, startY2 + r4, startZ2], //28
      [startX2 + r4, startY2, startZ2 + r4], //24
      //back top left pole 53-55
      [startX2, startY2 + r3 - r4, startZ2 + r4], //18
      [startX2 + r4, startY2 + r3, startZ2 + r4], //22
      [startX2 + r4, startY2 + r3 - r4, startZ2], //30
    ];
    const numVertices = vertices.length;
    const positions = new Float32Array(numVertices * 3);
    let posNdx = 0;
    for (const vertex of vertices) {
      positions.set(vertex, posNdx);
      posNdx += 3;
    }
    return positions;
  }, [r]);

  useEffect(() => {
    const buildFaces = (p1, p2, p3, p4, p5, p6, p7, p8) => {
      index.push(p1, p2, p3);
      index.push(p2, p4, p3);
      index.push(p2, p8, p4);
      index.push(p4, p8, p6);
      index.push(p6, p8, p7);
      index.push(p6, p7, p5);
      index.push(p1, p5, p7);
      index.push(p1, p3, p5);
    };
    const buildPole = (p1, p2, p3, p4, p5, p6) => {
      index.push(p1, p5, p2);
      index.push(p1, p4, p5);
      index.push(p3, p6, p5);
      index.push(p3, p5, p2);
    };
    const index = [];
    //front face
    buildFaces(4, 5, 8, 9, 10, 11, 6, 7);
    //right face
    buildFaces(5, 1, 12, 13, 14, 15, 7, 3);
    //left face
    buildFaces(0, 4, 16, 17, 18, 19, 2, 6);
    //top face
    buildFaces(6, 7, 20, 21, 22, 23, 2, 3);
    //bottom face
    buildFaces(0, 1, 24, 25, 26, 27, 4, 5);
    //back face
    buildFaces(0, 1, 28, 29, 30, 31, 2, 3);
    //front top right pole
    buildPole(11, 21, 14, 32, 33, 34);
    //front bottom right pole
    buildPole(9, 12, 27, 35, 36, 37);
    //front bottom left pole
    buildPole(8, 17, 26, 38, 39, 40);
    //front top left pole
    buildPole(19, 20, 10, 41, 42, 43);
    //back top right pole
    buildPole(31, 23, 15, 44, 45, 46);
    //back bottom right pole
    buildPole(25, 13, 29, 47, 48, 49);
    //back bottom left pole
    buildPole(16, 28, 24, 50, 51, 52);
    //back top left pole
    buildPole(18, 22, 30, 53, 54, 55);

    ref.current.setIndex(index);
  }, []);
  return (
    <bufferGeometry
      ref={ref}
      groups={[
        { start: 0, count: 146, materialIndex: 0 },
        { start: 147, count: 100, materialIndex: 1 },
      ]}
    >
      <bufferAttribute
        attachObject={["attributes", "position"]}
        args={[positions, 3]}
      />
    </bufferGeometry>
  );
}
