import React from "react";
import * as THREE from "three";
import { useData } from "./useData";
import TesseractGeometry from "./TesseractGeometry";

export default function InfiniteBoxes({ length }) {
  const meshRef = useData(length);

  return (
    <>
      <instancedMesh
        ref={meshRef}
        args={[null, null, length * 4 * length * length]}
        frustumCulled={false}
        position={[0, 0, -5]}
      >
        <TesseractGeometry r={1} />

        <meshBasicMaterial
          attachArray="material"
          color="#3F8787"
          transparent
          opacity={0.4}
          side={THREE.DoubleSide}
          depthFunc={THREE.GreaterEqualDepth}
          blending={THREE.AdditiveBlending}
          depthWrite={false}
        />
        <meshBasicMaterial
          attachArray="material"
          color="#386060"
          transparent
          opacity={0.4}
          side={THREE.DoubleSide}
          depthFunc={THREE.GreaterEqualDepth}
          blending={THREE.AdditiveBlending}
          depthWrite={false}
        />
      </instancedMesh>
      <mesh position={[0, 0, 0]} renderOrder={2}>
        <TesseractGeometry r={1} />
        <meshBasicMaterial
          attach="material"
          color="turquoise"
          side={THREE.DoubleSide}
          depthFunc={THREE.GreaterEqualDepth}
        />
      </mesh>
    </>
  );
}
