import "./App.css";
import { Canvas } from "@react-three/fiber";
import { OrbitControls, Stats, Loader } from "@react-three/drei";

import Sky from "./Components/Sky/Sky";
import InfiniteBoxes from "./Components/InfiniteBoxes/InfiniteBoxes";
import OuterBox from "./Components/OuterBox/OuterBox";
import Effects from "./Components/Effects/Effects";
import { useRef } from "react";
import Rig from "./Components/Rig/Rig";

export default function App() {
  const mouse = useRef([0, 0]);

  const onMouseMove = (e) => {
    mouse.current = [
      2500 * (e.clientX / window.innerWidth - 0.5),
      1500 * (e.clientY / window.innerHeight - 0.5),
    ];
  };

  return (
    <div className="container" onPointerMove={onMouseMove}>
      <Canvas camera={{ position: [0, 0, 3], fov: 40, far: 10000 }}>
        <Stats />
        <InfiniteBoxes length={12} />
        <OuterBox />
        <Sky />
        <Effects />
        {/* <OrbitControls /> */}
        <Rig mouse={mouse} />
      </Canvas>
      <Loader />
    </div>
  );
}
